import http from "../http-common";
import ITutorialData from "../types/Tutorial";

const getAll = () => {
  return http.get("/tutorial");
};

const get = (id: any) => {
  return http.get(`/tutorial/${id}`);
};

const create = (data: ITutorialData) => {
  return http.post("/tutorial", data);
};

const update = (id: any, data: ITutorialData) => {
  return http.put(`/tutorial/${id}`, data);
};

const remove = (id: any) => {
  return http.delete(`/tutorial/${id}`);
};

const removeAll = () => {
  return http.delete(`/tutorial/all`);
};

const findByTitle = (title: string) => {
  return http.get(`/tutorial/find/${title}`);
};

const TutorialService = {
  getAll,
  get,
  create,
  update,
  remove,
  removeAll,
  findByTitle,
};

export default TutorialService;